// TiktokViewer.js
import Hls from "hls.js";
import React, { useEffect, useRef, useState } from "react";
import Loader from "./Loader";

const TiktokViewer = () => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [soundOn, setSoundOn] = useState(false);
  const videoRefs = useRef([]);

  useEffect(() => {
    fetch("https://offermeqa.com/data/products_data.json")
      .then((res) => res.json())
      .then((data) => {
        const videos = data.filter((item) => item.type === "video");
        setProducts(videos);
        setLoading(false);
      })
      .catch((err) => {
        console.error("  :", err);
        setLoading(false);
      });
  }, []);

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          const video = entry.target.querySelector("video");
          if (!video) return;
          if (entry.isIntersecting) {
            video.play().catch(() => {});
          } else {
            video.pause();
          }
        });
      },
      { threshold: 0.8 }
    );

    videoRefs.current.forEach((ref) => {
      if (ref) observer.observe(ref);
    });

    return () => observer.disconnect();
  }, [products]);

  const toggleLike = (index) => {
    const btn = document.getElementById(`like-${index}`);
    if (!btn) return;

    const liked = btn.dataset.liked === "true";
    btn.textContent = liked ? "" : "";
    btn.dataset.liked = liked ? "false" : "true";
  };

  return (
    <div style={containerStyle}>
      <button onClick={() => setSoundOn(!soundOn)} style={soundBtnStyle}>
        {soundOn ? " " : " "}
      </button>

      {loading ? (
        <Loader />
      ) : (
        products.map((item, index) => (
          <div
            key={item.id}
            ref={(el) => (videoRefs.current[index] = el)}
            style={slideStyle}
          >
            <video
  ref={(el) => {
    if (el && item.url.endsWith(".m3u8")) {
      if (Hls.isSupported()) {
        const hls = new Hls();
        hls.loadSource(item.url);
        hls.attachMedia(el);
      } else if (el.canPlayType("application/vnd.apple.mpegurl")) {
        el.src = item.url;
      }
    } else if (el) {
      el.src = item.url;
    }
  }}
  muted={!soundOn}
  autoPlay
  loop
  playsInline
  style={videoStyle}
/>

            {/*    */}
            <div style={rightButtonsStyle}>
              <img src={item.userImage} alt="user" style={userImageStyle} />
              <button
                id={`like-${index}`}
                data-liked="false"
                onClick={() => toggleLike(index)}
                style={iconBtn}
              >
                
              </button>
              {item.affiliate && (
                <button
                  onClick={() => window.open(item.affiliate, "_blank")}
                  style={iconBtn}
                >
                  
                </button>
              )}
              {item.whatsapp && (
                <button
                  onClick={() => window.open("https://wa.me/" + item.whatsapp, "_blank")}
                  style={iconBtn}
                >
                  
                </button>
              )}
              <button style={iconBtn}></button>
              <button style={iconBtn}></button>
              <button onClick={() => setSoundOn(!soundOn)} style={iconBtn}>
                {soundOn ? "" : ""}
              </button>
            </div>

            {/*     */}
            <div style={overlayStyle}>
              <h3>{item.name}</h3>
              <p>{item.description}</p>
            </div>
          </div>
        ))
      )}
    </div>
  );
};

const containerStyle = {
  width: "100%",
  height: "100vh",
  overflowY: "scroll",
  scrollSnapType: "y mandatory",
  backgroundColor: "#000"
};

const slideStyle = {
  scrollSnapAlign: "start",
  height: "100vh",
  width: "100vw",
  position: "relative",
  overflow: "hidden"
};

const videoStyle = {
  width: "100vw",
  height: "100vh",
  objectFit: "cover",
  position: "absolute",
  top: 0,
  left: 0,
  zIndex: 1
};


const overlayStyle = {
  position: "absolute",
  bottom: "160px",
  left: "20px",
  color: "#fff",
  zIndex: 10,
  paddingRight: "80px"
};

const rightButtonsStyle = {
  position: "absolute",
  right: "10px",
  bottom: "200px",
  display: "flex",
  flexDirection: "column",
  gap: "12px",
  alignItems: "center",
  zIndex: 999
};

const iconBtn = {
  fontSize: "22px",
  background: "rgba(0,0,0,0.6)",
  border: "none",
  borderRadius: "50%",
  width: "48px",
  height: "48px",
  color: "#fff",
  cursor: "pointer"
};

const userImageStyle = {
  width: "54px",
  height: "54px",
  borderRadius: "50%",
  border: "2px solid #fff",
  objectFit: "cover"
};

const soundBtnStyle = {
  position: "absolute",
  bottom: "10px",
  right: "10px",
  zIndex: 999,
  background: "#000",
  color: "#fff",
  border: "none",
  borderRadius: "10px",
  padding: "8px 14px",
  fontSize: "14px",
  cursor: "pointer",
  boxShadow: "0 0 10px rgba(0,0,0,0.3)"
};

export default TiktokViewer;
