// Footer.js
import React from "react";

const Footer = () => {
  return (
    <footer className="tiktok-footer" style={footerStyle}>
      <button></button>
      <button id="searchByBusinessTypeBtn"></button>
      <button id="plusButton" className="plus-btn"></button>
      <button onClick={() => window.location.href = "/featured"}></button>
      <button onClick={() => window.location.href = "/my_profile"}>
        <img
          src="/static/img/user.png"
          alt=""
          style={{ width: "35px", height: "35px" }}
        />
      </button>
    </footer>
  );
};

const footerStyle = {
  position: "fixed",
  bottom: 0,
  left: 0,
  width: "100%",
  display: "flex",
  justifyContent: "space-around",
  alignItems: "center",
  backgroundColor: "black",
  padding: "10px 0",
  zIndex: 1000,
};

export default Footer;
